.service-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  text-align: center;
  background-color: #fff;
  transition: transform 0.2s ease-in-out;
}

.service-card:hover {
  transform: scale(1.05);
  border-color: #0078d7;
}

.service-card h3 {
  margin-bottom: 10px;
}
